import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'react-router-dom';
import axios from 'axios';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      customers: []
    };
  }

  componentDidMount() {
    axios.get('/customers')
      .then(res => {
        this.setState({ customers: res.data });
        console.log(this.state.customers);
      });
  }

  render() {
    return (
      <div class="container">
        <div class="panel panel-default">
          <div class="panel-heading">
            <h3 class="panel-title">
              CUSTOMERS LIST
            </h3>
          </div>
          <div class="panel-body">
            <h4><Link to="/calculate"><span class="glyphicon glyphicon-plus-sign" aria-hidden="true"></span> Calculate Credit Score And Add Customer </Link></h4>
            <table class="table table-stripe">
              <thead>
                <tr>
                  <th>TCKN</th>
                  <th>NameSurname</th>
                  <th>Salary</th>
                  <th>Phone</th>
                </tr>
              </thead>
              <tbody>
                {this.state.customers.map(c =>
                  <tr>
                    <td><Link to={`/show/${c.id}`}>{c.tckn}</Link></td>
                    <td>{c.nameSurname}</td>
                    <td>{c.monthlySalary}</td>
                    <td>{c.phoneNumber}</td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }
}

export default App;